/*헤더*/
.app_header{
  width: 100%;
  height: 10vw;
  padding: 5% 5% 0 5%;
  box-sizing: border-box;
  background-color: #FEF9A7;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.app_header>h1{
  margin: 0;
  font-size: 3vw;
  /* font-weight: lighter; */
  font-style: italic;
  color: black;
}
.app_header>nav{
  width: 40%;
  height: inherit;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-size: 2vw;
  /* font-weight: lighter; */
  color: black;
  letter-spacing: 1px;
}
@media screen and (min-width:768px){
  .app_header{
    background-color: mediumslateblue;
    height: 5vw;
  }
}

/*섹션1*/
.sec_intro{
  background-color: #FEF9A7;
  width: 100%;
  height: 60vw;
  padding: 15% 5% 0 5%;
  box-sizing: border-box;
}
.sec_intro>p{
  /* background-color: darksalmon; */
  margin: 0;
  width: 100%;
  height: 100%;
  line-height: 9vw;
  font-size: 4vw;
  /* font-weight: bolder; */
  color: black;
  letter-spacing: 8px;
}

/*풋터*/
.app_footer{
  background-color: #FEF9A7;
  width: 100%;
  height: 5vw;
  text-align: center;
  font-size: 1.5vw;
  color: rgba(0, 0, 0, 0.5);
}
